.profile__wrapper {
	max-width: 100%;
	display: flex;
	align-items: baseline;
}

@media screen and (max-width: 768px) {
	.profile__wrapper {
		width: 100%;
		margin: 0 auto;
		justify-content: center;
	}
}

@media screen and (max-width: 425px) {
	.profile__wrapper {
		max-width: 220px;
	}
}
