.spinner {
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  transform: rotate(45deg);
  transform-origin: 40px 40px;
  z-index: 10;
}
.spinner div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: lime;
  animation: spinner 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}
.spinner div:after,
.spinner div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: lime;
}
.spinner div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}
.spinner div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}
@keyframes spinner {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}